import API from "./API";

export const categorySearch = async (categoryIdx) => {
  const data = await API.get(`photographer/category/${categoryIdx}`)
    .then(res => {
      return res.data
    })
    .catch(err => {
      return (
        [
          {
            "photographerIdx": 1,
            "user": {
              "userIdx": 1,
              "name": "임우송",
              "email": "kingdomduner@gmail.com",
              "isPhotographer": true
            }
          },
          {
            "photographerIdx": 2,
            "user": {
              "userIdx": 11,
              "name": "김지명",
              "email": "kim-pick@gmail.com",
              "isPhotographer": true
            }
          },
        ]
      )
    })
  return data
}

export const textSearch = async (text) => {
  const data = await API.get('photographer/search', {
    params: {
      keyword: text
    }
  })
    .then(res => {
      return res.data
    })
  return data
}